.AppointmentPicker {
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.AppointmentChecker {
    width: 70%;
    height: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

.AppointmentContainer {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-top: 2px solid black;
    border-bottom: 2px solid black; 
}

.GetAppointmentButton{
    font-size: 12px;
    padding: 3px 3px;
    border: 0;
    background-color: #3968be;
    color: #fff;
    border-radius: 3px;
}

@media (max-width: 800px) {
    .AppointmentContainer {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-top: 2px solid black;
    }

    .AppointmentPicker {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }

    .AppointmentChecker {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px;
    }
  }